<template>
    <div class="home-wrapper">
        <blog-list :blogList="blogList"></blog-list>
        <infinite-loading :on-infinite="getMoreBlogs" ref="infiniteLoading">
            <span slot="no-more">没有更多了！</span>
        </infinite-loading>
    </div>
</template>

<script>

import { mapState, mapActions } from 'vuex';
import InfiniteLoading from 'vue-infinite-loading/src/components/Infiniteloading.vue';
import BlogList from '@/components/BlogList';

export default {
    name: 'home',
    props: {},
    data() {
        return {};
    },
    components: {
        InfiniteLoading,
        BlogList
    },
    computed: {
        ...mapState('blogs', [
            'blogList',
            'pageSize',
            'loadingStatus'
        ])
    },
    methods: {
        ...mapActions('appShell/appHeader', [
            'setAppHeader'
        ]),
        ...mapActions('blogs', [
            'getBlogList'
        ]),
        async getMoreBlogs() {
            let type = this.$route.query.type || '';
            await this.getBlogList({
                type,
                pageNum: Math.floor(this.blogList.length / this.pageSize) + 1,
                pageSize: this.pageSize
            });
            this.$refs.infiniteLoading.$emit('$InfiniteLoading:' + this.loadingStatus);
            // if (this.blogList.length <= 2) {
            //     this.$refs.infiniteLoading.$el.remove();
            // }
        }
    },
    watch: {
        ['$route.query.type'](newtype, oldtype) {
            document.body.scrollTop = 0;
        }
    },
    mounted () {
        this.$refs.infiniteLoading.$emit('$InfiniteLoading:loaded');
        let title = 'welcome to my blog site';
        let type = this.$route.query.type || '';
        if (type) {
            title = `类别-${type}`;
        }
        this.setAppHeader({
            show: true,
            showMenu: true,
            title: title,
            showBack: false,
            showLogo: true,
            actions: [
                {
                    icon: 'search',
                    route: '/search'
                }
            ]
        });
    },
    // async asyncData({ store, route }) {
    //     let type = route.query.type || '';
    //     store.dispatch('blogs/getBlogList', {
    //         type,
    //         pageNum: 0,
    //         pageSize: store.state.blogs.pageSize;
    //     });
    // }
};
</script>

<style lang="stylus" scoped>
    @import '~@/assets/styles/variables';
    @import '~vuetify/src/stylus/settings/_colors';

    $theme := {
        primary: rgb(40, 116, 240)
        accent: $blue.accent-2
        secondary: $grey.darken-3
        info: $blue.base
        warning: $amber.base
        error: $red.accent-2
        success: $green.base
    }

    $material-custom := {
        bg-color: #fff
        fg-color: #000
        text-color: #000
        primary-text-percent: 0.87
        secondary-text-percent: 0.54
        disabledORhints-text-percent: 0.38
        divider-percent: 0.12
        active-icon-percent: 0.54
        inactive-icon-percent: 0.38
    }
    $material-theme := $material-custom


</style>



